import AceEditor, { IMarker } from "react-ace";
import axios from "axios";
import React, { useState, useEffect, useRef } from "react";
import FileSaver from "file-saver";
import "./theme";
import {
  StateText,
  Space,
  Title,
  SubTitle,
  VimBox,
  VimText,
  CheckBox,
  Button,
  ButtonBox,
  TopBox,
} from "./Components";
import styled from "styled-components";

import WSCMode from "./mode.js";
import template from "./template";
import //playNewAudio, fadeOutSource,
//displayError,
"./utils";
import Settings from "../settings";

import "ace-builds/src-noconflict/mode-elixir";
import "ace-builds/src-noconflict/theme-github";
import "ace-builds/src-noconflict/keybinding-vim";

const BACKEND_RENDER_URL = `${Settings.backend}/render`;
const customMode = new WSCMode();

enum State {
  Cool,
  Rendering,
}

function Compose() {
  const [state, setState] = useState<State>(State.Cool);
  const [save, setSave] = useState<boolean>(false);
  const [vim, setVim] = useState<boolean>(true);
  const [renderSpace, setRenderSpace] = useState<AceEditor | null>();
  const [fileName, setFileName] = useState<string>("");
  const [language, setLanguage] = useState<string>(template);

  const [render, setRender] = useState<boolean>(false);

  const [markers, setMarkers] = useState<IMarker[]>([]);

  const getStoredLanguage = () => {
    const stored = localStorage.getItem("language");
    if (stored) {
      setLanguage(stored);
    }
  };

  useEffect(() => {
    const submit = async () => {
      if (render) {
        setMarkers([]);

        try {
          setState(State.Rendering);
          let response = await axios.post(BACKEND_RENDER_URL, { language });
          if (renderSpace) {
            //switch (response.data.response_type) {
            //case "RenderSuccess":
            localStorage.setItem("language", language);
            //break;
            //case "RenderError":
            //const error = response.data.error;
            //const n_lines = language.split("\n").length;
            //displayError(error, n_lines, renderSpace, setMarkers);
            //break;
            //default:
            //console.log("Not sure how we got here...");
            //console.log(response);
            //break;
            //}
          }
        } catch (err) {
          console.log(err);
        }

        setState(State.Cool);
        setRender(false);
      }
    };

    submit();
  }, [render, language, renderSpace]);

  useEffect(() => {
    if (renderSpace) {
      renderSpace.editor.getSession().setMode(customMode);
      renderSpace.editor.setTheme("ace/theme/wsc");
    }
  }, [renderSpace]);

  useEffect(() => {
    getStoredLanguage();
  }, []);

  useEffect(() => {
    if (save) {
      saveFile();
      setSave(false);
    }
  }, [save]);

  const stopAudio = () => {
    //fadeOutSource(audioCtx, sources[(node + 1) % 2], gainNodes[(node + 1) % 2]);
  };

  const RenderState = () => {
    if (state === State.Rendering) {
      return <StateText>Rendering</StateText>;
    } else {
      return <div />;
    }
  };

  const loadFile = (e: any) => {
    let file = e.target.files[0];
    var reader = new FileReader();
    reader.onload = function (event: any) {
      if (event.target) {
        setFileName(file.name);
        setLanguage(event.target.result);
      }
    };

    reader.readAsText(file);
  };

  const saveFile = () => {
    var blob = new Blob([language], {
      type: "text/plain;charset=utf-8",
    });
    FileSaver.saveAs(blob, fileName);
  };
  const fileInput = useRef<HTMLInputElement>(null);

  const handleLoad = () => {
    if (fileInput && fileInput.current) {
      fileInput.current.click();
    }
  };

  return (
    <Space>
      <Title>WereSoCool</Title>
      <SubTitle>Make cool sounds. Impress your friends/pets/plants.</SubTitle>
      <TopBox>
        <input
          ref={fileInput}
          type="file"
          accept=".socool"
          style={{ display: "none", visibility: "hidden" }}
          onChange={(e) => loadFile(e)}
        />
        <ButtonBox>
          <Button onClick={() => setRender(true)}>Render</Button>
          <Button onClick={() => stopAudio()}>Stop</Button>
          <Button onClick={handleLoad}>Load</Button>
          <Button onClick={saveFile}>Save</Button>
          <RenderState />
        </ButtonBox>
        <VimBox>
          <Button onClick={() => setLanguage(template)}>Reset</Button>
          <VimText>{vim ? "Vim!" : "Vim?"}</VimText>
          <CheckBox
            name="Vim"
            type="checkbox"
            checked={vim ? true : false}
            onChange={() => setVim(!vim)}
          />
        </VimBox>
      </TopBox>
      <AceEditor
        focus={true}
        ref={(el) => {
          setRenderSpace(el);
        }}
        placeholder="WereSoCool"
        mode="elixir"
        theme="github"
        name="aceEditor"
        keyboardHandler={vim ? "vim" : ""}
        value={language}
        onChange={(l) => setLanguage(l)}
        markers={markers}
        fontSize={20}
        showPrintMargin={true}
        showGutter={true}
        highlightActiveLine={true}
        setOptions={{
          showLineNumbers: true,
          tabSize: 2,
        }}
        commands={[
          {
            name: "submit",
            bindKey: { win: "Shift-Enter", mac: "Shift-Enter" },
            exec: () => {
              setRender(true);
            },
          },
          {
            name: "save",
            bindKey: { win: "Command-s", mac: "Command-s" },
            exec: () => {
              setSave(true);
            },
          },
        ]}
        style={{
          height: "80vh",
          width: "80vw",
          marginLeft: "10vw",
        }}
      />
    </Space>
  );
}

export default Compose;
